.hero{
    display: flex;
    justify-content: space-between;
}



.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 3;
    display: flex;
    gap: 2rem;
    flex-direction: column;
}
.right-h{
    flex: 1;
    position: relative;
    background-color: rgb(42, 42, 66);
}

.the-best{
    margin-top: 4rem;
    background-color: gray;
    border-radius: 4rem;
    width: fit-content;
    padding:20px 13px;
    text-transform: uppercase;
    color: white;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.the-best>span{
    z-index: 2;
}
.the-best>div{
    position: absolute;
    background-color:  rgb(42, 42, 66);
    width:5.4rem;
    height: 80%;
    left:8px;
    border-radius: 3rem;
    z-index: 1;
}



.hero-text{
    display: flex;
    flex-direction: column;
    gap:1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;
}

.hero-text>div:nth-of-type(3){
    font-size: 1rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    width:80%;
}


.figures{
    display: flex;
    gap:2rem;

}

.figures>div{
    display: flex;
    flex-direction: column;
}

.figures>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;
    text-transform: capitalize;
}


.figures>div>span:nth-of-type(2){
    color:lavender;
    text-transform: uppercase;
}

.hero-buttons{
    display: flex;
    gap:1rem;
    font-weight: normal;
}


.hero-buttons>:nth-child(1){
    color: white;
    background-color:  rgb(42, 42, 66);
    width: 8rem;
}

.hero-buttons>:nth-child(2){
    color: white;
    background-color:transparent;
    width: 8rem;
    border: 2px solid   rgb(42, 42, 66);
}


.right-h>.btn{
    position: absolute;
    right:3rem;
    top:2rem;
    color: black;
    border-radius: 20px;
}

.heart-rate{
    display: flex;
    flex-direction:column;
    gap:1rem;
    background-color:darkGray;
    width: fit-content;
    padding: 1rem;
    align-items: start;
    border-radius: 20px;
    position: absolute;
    right: 4rem;
    top:7rem;
    opacity:0.9;
}

.heart-rate>img{
    width: 4rem;
    
}

.heart-rate>span:nth-child(2){
 color:hsla(0, 0%, 9%, 0.823);
}

.heart-rate>span:nth-child(3){
    color: white;
    font-size: 1.5rem;
   }


   .hero-image{
    position: absolute;
    top:2.3rem;
    right: 1rem;
    width: 48rem;
  
   
   }


   .tcoin{
    display: flex;
    gap: 2rem;
    background-color:hsla(0, 0%, 66%, 0.823) ;
    border-radius: 30px;
    padding: 1rem;
    width: fit-content;
    position: absolute;
    top:32rem;
    right: 40rem;
   }

   .tcoin>img{
    width: 5rem;
   

   }

   .tcoin>div{
    display: flex;
    flex-direction: column;
    justify-content:space-between ;
   }


   .tcoin>div>:nth-child(1){
    color:rgb(28, 28, 29);
   }

   .tcoin>div>:nth-child(2){
    color:white;
    font-size: 1.5rem;
   }

   .hero-blur{
    width: 22rem;
    height: 30rem;
    left: 0;
   }


   @media screen and (max-width: 768px)
   {
    .hero{
        flex-direction: column;
    }
    .hero-blur{
        width: 14rem;
    }
   
    .the-best{
        margin-top: 0;
        font-size: small;
        align-self: center;
        transform: scale(0.8);
    }

   .hero-text{
    font-size: x-large;
    align-items: center;
    justify-content: center;
   }

   .hero-text>div:nth-of-type(3){
    font-size:x-small;
    font-weight: 200;
    letter-spacing: 1px;
    text-align: center;
   }

   .hero-buttons{
    justify-content: center;
   }
   
   .figures>div>span:nth-of-type(1){
    font-size: large;
   }

   .figures>div>span:nth-of-type(2){
    font-size:x-small;
   }

   .right-h{
    position: relative;
    background: none;
   }

   .heart-rate{
    left: 1rem;
    right: 2rem;
   }


   .tcoin{
    position: relative;
    top: 5rem;
    left: 2rem;
   }

   .tcoin>img{
    width: 2rem;
   }
   .tcoin>div>:nth-child(2){
    color: azure;
    font-size: 1rem;
   }


   .hero-image{
   position: relative;
   width: 30rem;
   right:5rem;
   top:6rem;
   height:35rem;
  
   }

   .heart-rate>img{
    width: 2rem;
    
